import {MEMBER_LIST_SUCCESS} from '../action/Action'

export default function members(state =[], action){
    switch (action.type){
        case MEMBER_LIST_SUCCESS:
            console.log(action.data);
            return [ ...state, {id:'m3', firstname:'mmmmm333', lastname:'mmmmm333'}];
        default:
            return state;
    }
}



